<template>
    <div>
        <hader/>
        <div>
            <div class ="banner">
                <!-- <div class="tit">[ 让每一个商户都成为我们的朋友 ]</div>
                <div class="cont">
                    <span>网址：www.dabojituan.com </span>
                    <span> 邮箱：dbzhpt@gmail.com</span>
                    <div>接待时间：7x24 小时</div>
                </div> -->
            </div>            
            <div class="productCont">
              <div class="tit">产品介绍</div>
              <div class="list">
                  <div class="imgBox sports">
                    <div class="nm">体育游戏</div>
                    <div>Sports games</div>
                  </div>
                  <div class="imgBox electron">
                    <div class="nm">电子游艺</div>
                    <div>Electronics games</div>                      
                  </div>
                  <div class="imgBox fishing">
                    <div class="nm">捕鱼游戏</div>
                    <div>fishing</div>                      
                  </div>
                  <div class="imgBox immortal">
                    <div class="nm">真人视讯</div>
                    <div>Live Video</div>                      
                  </div>
                  <div class="imgBox lottery">
                    <div class="nm">彩票游戏</div>
                    <div>lottery game</div>                      
                  </div>
                  <div class="imgBox more">
                    <div class="nm"> 更多游戏</div>
                    <div>More games</div>                      
                  </div>
              </div>  
            </div>
        </div> 
        <footera/>
        <div class="nav row average">
            <div :class="{active:nav == 'index'}" @click="navClick('index')">
                <div>
                    <img :src="nav == 'index'?img.indexa:img.index" >
                </div>
                <div>
                    <p>首页</p>
                </div>
            </div>
            <div class="column average"  :class="{active:nav == 'product'}" @click="navClick('product')">
                <div>
                    <img :src="nav == 'product'?img.producta:img.product">
                </div>
                <div>
                    <p>产品</p>
                </div>
            </div>
            <div class="column average" :class="{active:nav == 'advantage'}" @click="navClick('advantage')"> 
                <div>
                    <img :src="nav == 'advantage'?img.advantagea:img.advantage">
                </div>
                <div>
                    <p>优势</p>
                </div>
            </div>
            <div class="column average" :class="{active:nav == 'partner'}" @click="navClick('partner')">
                <div>
                    <img :src="nav == 'partner'?img.partnera:img.partner">
                </div>
                <div>
                    <p>伙伴</p>
                </div>
            </div>
            <div class="column average" :class="{active:nav == 'customized'}" @click="navClick('customized')">
                <div>
                    <img :src="nav == 'customized'?img.creationa:img.creation">
                </div>
                <div>
                    <p>定制</p>
                </div>  
            </div>
            <div class="column average" :class="{active:nav == 'about'}" @click="navClick('about')">
                <div>   
                    <img :src="nav == 'about'?img.abouta:img.about">
                </div>
                <div>
                    <p>关于</p>
                </div>
            </div>
        </div>

    </div>
</template>
<script>
import hader from '@/components/header';
import footera from '@/components/footer';
export default {
    name:'product',
    components:{
        hader,footera
    },
    data(){
        return{
            list:[
                {
                    title : '体育游戏',
                    cont : 'Sports games'
                },
                {
                    title:'电子游艺',
                    cont : 'Electronics games'
                },
                {
                    title : '捕鱼游戏',
                    cont : 'fishing'
                },
                {
                    title : '真人视讯',
                    cont : 'Live Video'
                },
                {
                    title : '彩票游戏',
                    cont : 'lottery game'
                }
            ],
            data:'',
            nav:'product',
            img:{
                index:require('@/assets/st-imges/index.png'),
                indexa:require('@/assets/st-imges/indexa.png'),
                about:require('@/assets/st-imges/about.png'),
                abouta:require('@/assets/st-imges/abouta.png'),
                creation:require('@/assets/st-imges/creation.png'),
                creationa:require('@/assets/st-imges/creationa.png'),
                advantage:require('@/assets/st-imges/advantage.png'),
                advantagea:require('@/assets/st-imges/advantagea.png'),
                partner:require('@/assets/st-imges/partner.png'),
                partnera:require('@/assets/st-imges/partnera.png'),
                product:require('@/assets/st-imges/product.png'),
                producta:require('@/assets/st-imges/producta.png'),
            }
        }
    },
    methods:{
        navClick(str){
            // this.nav = str
            if(str == this.nav ) return false;
            let path = str;
            if(str == 'index'){
                path ='/'
            }
            this.$router.push({ path: path });
        }
    }
}
</script>
<style scoped>
    .banner{
        background: url("./../../assets/st-imges/productBanner.png") no-repeat;
        background-size: 100% 100%;
        background-color:rgba(20,10,3,1); 
        /* height: 1.3rem; */
        color: #fff;
        height: 2.3rem;
        padding: 0.54rem 0.5rem 0.64rem;
        margin-top: 1.28rem;
    }
    .banner .tit{
        text-align: center;
        font-size: 0.36rem;
        margin-bottom: 0.12rem;
    }
    .banner .cont{
        text-align: center;
        font-size: 0.26rem;
        line-height: 0.4rem;
    }  
    .productCont{
        margin: 0.4rem  0.2rem 0;
        text-align: center;
    }
    .productCont .tit{
        margin: 0.4rem 0 0.44rem;
        position: relative;
        font-size: 0.34rem;
        color: #555555;
        font-weight:bold;
    }
    .productCont .tit::after{
        display: inline-block;
        content: '';
        width: 0.6rem;
        height: 0.02rem;
        background: #E63440;
        position: absolute;
        bottom: -0.1rem;
        left: 46%;
    }
    .list{
        display: flex;
        flex-wrap:wrap;
        justify-content: space-between;
    }
/* 导航 */
    .nav{
        position: fixed;
        bottom: 0;
        left: 0;
        background: #fff;
        height: 1rem;
        width: 100%;
        padding: 0.1rem 0;
        border-top: 1px solid #dddddd;
    }
    .nav>div{
        width: 16%;
        height: 100%;
    }
    .nav>div>div{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
      
    }
    .nav>div img{
        width: .34rem;
        height: .34rem;
    }
    .active{
        color: #E63440;
    }
    /* 产品图片背景 */
    .imgBox{
        width: 30%;
        height: 2.8rem;
        margin-bottom:  0.25rem;
        /* margin: 0 0.25rem 0.25rem 0; */
        padding-top: 1.72rem;
    }
    .imgBox:nth-child(3n),.imgBox:nth-last-child(1){
        margin: 0 0 0.25rem 0;
    }
    .sports{
         background: url("./../../assets/st-imges/sports.png") no-repeat center center;
         background-size: 100% 100%;
    }
    .fishing{
         background: url("./../../assets/st-imges/fishing.png") no-repeat center center;
         background-size: 100% 100%;
    }
    .electron{
         background: url("./../../assets/st-imges/electron.png") no-repeat center center;
         background-size: 100% 100%;
    }
    .lottery{
         background: url("./../../assets/st-imges/lottery.png") no-repeat center center;
         background-size: 100% 100%;
    }
    .immortal{
         background: url("./../../assets/st-imges/immortal.png") no-repeat center center;
         background-size: 100% 100%;
    }
    .more{
        background: url("./../../assets/st-imges/more.png") no-repeat center center;
        background-size: 100% 100%;
    }
    .imgBox div{
        color: #E63440;
        font-size: 0.2rem;
    }
    .imgBox .nm{
        font-size: 0.32rem;
        font-weight: bold;
    }
</style>



